import css from "./NotFound.module.css";

export default function NotFound(props: { loggedIn: boolean }) {
    const { loggedIn } = props;
    if (!loggedIn) return null;

    return (
        <div className={css.container}>
            <div className={css.layout}>
                <h1 className={css.errorCode}>404</h1>
                <p className={css.message}>Oops! The page you're looking for doesn't exist.</p>
                <div className={css.animation}>
                    <div className={css.ball}></div>
                    <div className={css.shadow}></div>
                </div>
            </div>
        </div>
    );
}
